	module.exports = {"data":{"wordpressPost":{"title":"BMJ Open in the news","content":"<p>&nbsp;</p>\n<p><span style=\"font-family: Calibri\">BMJ Open research has made the news again. The <a href=\"http://bmjopen.bmj.com/content/2/4/e000828.full\" target=\"_blank\">paper by Peter T Katzmarzyk (</a><span style=\"font-size: small\"><a href=\"http://bmjopen.bmj.com/content/2/4/e000828.full\" target=\"_blank\">Pennington Biomedical Research Center) and I-Min Lee (Harvard)</a> on the impact of sitting and television viewing on life expectancy in the USA has received widespread coverage, <a href=\"http://www.bbc.co.uk/news/health-18767278\" target=\"_blank\">from the BBC </a>in the UK, through numerous US news outlets, and as far as the <a href=\"http://www.nzherald.co.nz/lifestyle/news/article.cfm?c_id=6&amp;objectid=10818794\" target=\"_blank\">New Zealand Herald</a>. At the time of writing it has also received <a href=\"http://bmjopen.bmj.com/articleusage?rid=2/4/e000828\" target=\"_blank\">over 5500 views</a>.</span></span></p>\n<p><span style=\"font-size: small;font-family: Calibri\">To give visitors to the site an idea of where BMJ Open papers are being discussed our homepage now includes a news feed – click on the &#8216;In the news&#8217; tab on <a href=\"http://bmjopen.bmj.com/\" target=\"_blank\">our homepage </a>and you will discover links to sites covering the latest BMJ Open research.</span><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"96c15648-8855-5545-b35b-316379412bff"}}

